.wrapper {
  margin: 100px 20%;
  font-family: serif;
  left: 20%;
  width: 60%;
  font-size: 20px;
}

.titlePage {
  text-align: center;
}

.explanation {
  padding: 30px 0;
  text-align: left;
}

.question {
  font-weight: bold;
  font-size: 30px;
  padding-bottom: 30px;
}

.pictureWrapper {
  margin-bottom: 20px;
}

.picture {
  max-width: 50%;
}

.textInput {
  font-size: 20px;
  border-color: black;
  border-width: 2px;
  border-radius: 5px;
}

.answer {
  padding: 5px 0;
}

.nextButton {
  margin: 30px 0;
}

.radioButton {
  margin: 0 10px 0 0;
}

button {
  margin-top: 10px;
  background: white;
  border-color: black;
  border-style: solid;
  border-radius: 5px;
  font-size: 20px;
  font-family: inherit;
  box-shadow: none;
}

button:disabled {
  border-color: gray;
}

